/*------------------------------------*
	@TRUMPS #LAYOUT
\*------------------------------------*/
@for $j from 0 through 10 {
    .mb-#{$j} {
	   margin-bottom: 0.5rem * $j !important;
	}
}

.mr-auto {
	margin-right: auto;
}
.ml-auto {
	margin-left: auto;
}

.d-flex {
	display: flex;
}

.flex-col {
	flex-direction: column;
}

.flex-1 {
	flex-grow: 1;
}