/*------------------------------------*
	@COMPONENTS #LAYOUT
\*------------------------------------*/

.main {
	flex: 1 0 auto;
	padding-top: 3rem;
	padding-bottom: 3rem;
}

.title {
	font-size: 2rem;
	font-weight: 700;
	text-align: center;
	position: relative;
	padding-bottom: 1rem;
	margin-bottom: 3rem;

	&::after {
		content: '';
		width: 4rem;
		height: 0.125rem;
		background-color: var(--primary);
		@include centerH;
		top: 100%;
		border: $radius;
	}
}