/*------------------------------------*
	@COMPONENTS #PAGINATION
\*------------------------------------*/

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination__btn {
    width: 1.5rem;
    height: 1.5rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: var(--text-alt);
    border-radius: 0.125rem;

    &:not(:last-child) {
        margin-right: .5rem;
    }

    .icon {
        width: 0.625rem;
        height: 0.625rem;
    }

    &--active {
        background-color: var(--border);
        color: var(--text);
    }
}