/*------------------------------------*
	@COMPONENTS #FORMS
\*------------------------------------*/

.form__input {
    color: $form-clr;
    border: 1px solid var(--border);
    background-color: var(--lighter);
    font-family: $base-font;
    font-size: $base-font-size;
    height: 2.5rem;
    padding-right: 1rem;
    padding-left: 1rem;
    border-radius: var(--radius);
    font-weight: 700;

    &::-webkit-input-placeholder {
       color: var(--form);
    }
    &::-moz-placeholder {
        color: var(--form);
        opacity: 1;  
    }
    &:-ms-input-placeholder {
        color: var(--form);  
    }
    &:not(textarea) {
        line-height: 38px;
    }

    &:hover,
    &:focus {
        // border-color: $primary;
    }

    &--width {
        width: 12rem;
    }

    .card & {
        background-color: #fff;
    }
}

textarea.form__input {
    resize: none;
}

.form__group {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;

    &--top {
        align-items: flex-start;

        .form__label {
            margin-top: 0.5rem;
        }
    }

    &--fix {
        .form__label {
            width: 9rem;
        }
    }

    &--between {
        justify-content: space-between;
    }
}

.form__label {
    text-align: right;

    .form__group & {
        margin-right: 1rem;
    }

    .card & {
        color: var(--text-alt);
    }
}

.form__block {
    flex: 1;
    display: flex;
    border-radius: var(--radius);
    border: 1px solid var(--border);

    .form__input {
        flex: 1;
        border: none;

        &:first-child {
            border-radius: var(--radius) 0 0 var(--radius);
        }
        
        &:last-child {
            border-radius: 0 var(--radius) var(--radius) 0;
        }

        &:only-child {
            border-radius: var(--radius);
        }
    }
}

.form__addon {
    width: 2.5rem;
    background-color: var(--light);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    
    &:first-child {
        border-right: 1px solid var(--border);
        border-radius: var(--radius) 0 0 var(--radius);
    }
    &:last-child {
        border-left: 1px solid var(--border);
        border-radius: 0 var(--radius) var(--radius) 0;
    }
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-overflow: '';

    &::-ms-expand {
        display: none;
    }
}

select.form__input {
    background: {
        image: url('../images/select-bgr.svg');
        position: center right;
        size: 39px 38px;
        repeat: no-repeat;
    }
}

textarea.form__input {
    min-height: 10rem;
}

