/*------------------------------------*
    @OBJECTS #BUTTONS
\*------------------------------------*/

button {
    cursor: pointer;
    @include trans;
    border: 1px solid var(--border);
    
    &,
    &:focus,
    &:active {
        outline: transparent !important;

        &:not(.btn) {
            border: none;
        }
    }
}

.btn {
    user-select: none;
    display: inline-block;
    vertical-align: middle;

    font: inherit;
    font-family: $base-font;
    text-align: center;
    text-transform: none;

    margin: 0;

    cursor: pointer;
    overflow: visible;
    @include trans;

    &,
    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
    }
}