/*------------------------------------*
	@COMPONENTS #BUTTONS
\*------------------------------------*/

button {
	box-shadow: none;
}

.btn-wrap {
	display: flex;
	width: 100%;

	> * {
		&:not(:last-child) {
			margin-right: 1rem;
		}
	}

	&--center {
		justify-content: center;
	}

	&--right {
		justify-content: flex-end;
	}
}

.btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	border-radius: var(--radius);
	padding: 0 16px;
	height: 40px;
	border-width: 1px;
	border-style: solid;
	outline: transparent;
	box-shadow: none !important;	
}

.btn--primary {
	&, &:focus {
		background-color: var(--primary);
		border-color: var(--primary);
		color: #fff !important;

		&:hover {
			background-color: var(--primary-dark);
		}
	}
}

.btn--alt {
	&, &:focus {
		background-color: #fff;
		border-color: var(--primary);
		color: var(--primary);

		&:hover {
			background-color: var(--primary);
			color: #fff;
		}
	}
}
