/*------------------------------------*
	@COMPONENTS #HEADER
\*------------------------------------*/

.header {
	background-color: #fff;
	box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.1);

	.container {
		height: 68px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}

	.header__logo {
		&:hover {
			opacity: .5;
		}

		img {
			height: 2.5rem;
		}
	}

	.header__nav {
		a:not(.btn) {
			color: $text-clr;

			&:hover {
				opacity: .5;
			}
		}

		> * {
			&:not(:last-child) {
				margin-right: 1.5rem;
			}
		}
	}