/*------------------------------------*
	@COMPONENTS #DIAGRAM
\*------------------------------------*/

.diagram-wrap {
    display: flex;
    border-radius: var(--radius);
    border: 1px solid var(--border);
    padding: 1rem;
    background-color: var(--light);
}

.diagram {
    flex: 1;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(9, 2.25rem);
    gap: .5rem;
    
    &:not(:last-child) {
        margin-right: 0.5rem;
    }
    
    &--md {
        grid-template-rows: repeat(8, 2.25rem);
    }
}

    .diagram__item {
        --diagram-color: var(--alt);
        // grid-template-columns: ;
        border-radius: .25rem;
        border: 1px solid var(--diagram-color);
        color: var(--diagram-color);
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        font-size: 0.875rem;
        position: relative;
        
        &--lg {
            grid-row-end: span 4;
        }
        &--md {
            grid-row-end: span 3;
        }
        &--sm {
            grid-row-end: span 2;
        }
        &--xs {
            grid-row-end: span 1;
        }

        &--empty {
            --diagram-color: var(--green);
        }
        &--faulty {
            --diagram-color: var(--red);
            background-color: var(--red-light);
        }
        &--screen {
            background: {
                image: url(../images/screen.png);
                repeat: no-repeat;
                position: center center;
            }
        }
        &--occupied {
            background-color: #fff;
            background: repeating-linear-gradient( -45deg, #f0f0f0, #f0f0f0 4px, #fff 4px, #fff 16px );
        }
        
        &--disabled {
            position: relative;

            .icon {
                position: absolute;
                bottom: 0.5rem;
                left: 0.5rem;
                color: var(--blue);
            }
        }

        &[data-nbr] {
            &::before {
                content: attr(data-nbr);
                position: absolute;
                top: 0.5rem;
                left: 0.5rem;
            }
        }
    }

    .diagram__popup {
        background-color: #fff;
        border: 1px solid var(--border);
        box-shadow: 0 4px 8px 0 rgba(#000,.15);
        @include centerH;
        top: 100%;
        margin-top: 0.25rem;
        z-index: 10;
        width: calc(100% + 4px);

        display: flex;
        flex-direction: column;
        padding: 0.75rem 1rem;
        border-radius: var(--radius);

        @include hide;
        @include trans;
        
        .diagram__item:hover & {
            @include show;
        }

        .btn {
            flex: 1;

            &:not(:last-child) {
                margin-right: .5rem;
            }
        }
    }

        .diagram__dl {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 0.5rem;
        }