//------------------------------------\\
//  @SETTINGS #VARIABLES
//------------------------------------//

//COLORS
:root {
    --primary: #F27543;
    --primary-dark: #c55b31;
    --alt: #333333;
    --border: #D6DADF;
    --lighter: #fcfcfc;
    --light: #F7F8FA;

    --text: #333;
    --text-alt: #767B81;
    --form: #333;

    --radius: 0.25rem;

    --green: #11C096;
    --red: #DF3030;
    --red-light: rgba(223,48,48,0.05);
    --blue: #16A3F2;
}

$primary: 					var(--primary);
$alt: 						var(--alt);

// Backgrounds
$bgr: 						#fff;
$base-bgr:                  #fff;


// Fonts
$base-font:					"Raleway", sans-serif !default;

$base-font-size:            16px;
$base-line-height:         	1.4;
$base-font-weight:          700;

$text-clr: 					var(--text);
$text-alt: 				    var(--text-alt);
$form-clr:				    var(--form);
$placeholder-clr:		    var(--form);
$link-clr:					var(--primary);
$link-hover:				var(--primary-dark);

$border:					var(--border);
$borderd:				    var(--alt);



// Layout
$space:                     30px;
$radius:					4px;
$grid-gutter:     			30px !default;


// Breakpoints
$screen-xxs:                480px;
$screen-xs:                 560px;
$screen-sm:                 768px;
$screen-md:                 992px;
$screen-lg:                 1200px;
$screen-xl:                 1440px;

$breakpoint:     			991px !default;

// Select Text
$selected-text-bgr:			#333;
$selected-text-clr:			#fff;