/*------------------------------------*
	@COMPONENTS #CARD
\*------------------------------------*/

.card-wrap {
    display: flex;
    flex-wrap: wrap;
    margin: -1rem;

    .card {
        width: calc(20% - 2rem);
        margin: 1rem;
    }
}

.card {
    display: flex;
    flex-direction: column;
    background: var(--lighter);

    border: 1px solid var(--border);
    border-radius: 8px;
    @include trans;
    
    > * {
        display: flex;
        flex-direction: column;
        padding: 1em;
        @include trans;
    }
}

    .card__header {
        background-color: var(--light);
        border-bottom: 1px solid var(--border);
        justify-content: center;
        border-radius: var(--radius) var(--radius) 0 0;
    }

        .card__title {
            text-align: center;
            font-weight: 700;
            font-size: 1rem;
        }

    .card__body {
        &--split {
            flex: 1;
            flex-direction: row;
            padding: 0 !important;
        }
    }

        .card__info {
            display: flex;
            justify-content: space-between;

            dt {
                color: var(--text-alt);
                margin-right: 0.5rem;
            }

            dd {
                text-align: right;
                color: var(--text);
                margin-left: 0;
            }
            
            .card__body & {
                &:not(:last-child) {
                    margin-bottom: 0.25rem;
                }
            }

            &--block {
                flex-direction: column;

                dd {
                    text-align: left;
                }

                .card__body & {
                    &:not(:last-child) {
                        margin-bottom: 0.5rem;
                    }
                }
            }
        }

            .card__circle {
                width: 0.5rem;
                height: 0.5em;
                border-radius: 50%;
                display: inline-block;
            }

    .card__footer {
        background-color: var(--light);
        border-top: 1px solid var(--border);

        > * {
            &:not(:last-child) {
                margin-bottom: 0.5rem;
            }
        }
    }

        .card__split {
            padding: 1.5rem;
            flex: 1;
            
            &:not(:last-child) {
                border-right: 1px solid var(--border);
            }
        }