/*------------------------------------*
	@COMPONENTS #PILLS
\*------------------------------------*/

.pills-wrap {
    display: flex;

    .pill {
        &:not(:last-child) {
            margin-right: 0.5rem;
        }
    }
}

.pill {
    background-color: var(--border);
    color: var(--text-alt);
    border-radius: .25rem;
    font-weight: 700;
    font-size: 0.875rem;
    padding: 0.5rem 0.75rem;
    
    &--active {
        background-color: var(--text-alt);
        color: #fff;
    }
}